.App {
  min-height: calc(100vh - 40px);
  width: 85vw;
  margin: 0 auto;
  padding: 0 30px 0 35px;
}

footer {
  border-top: 1px solid darkgray;
  width: 95vw;
  padding: 5px 0 10px 0;
  margin: 0 auto;
  text-align: center;
}

.Header {
  padding: 10px 0 5px 0;
  margin: 5px 0 10px 0;
  border-bottom: 1px solid darkgray;
  width: 100%;
}

.Header button, .Header input {
  margin: 5px 0 0 5px;
  padding: 3px;
}

.Header button {
  width: 100px;
}

.Header input {
  min-width: 205px;
  width: calc(100% - 240px);
}
